import PropTypes from "prop-types";
import Breadcrumb from 'react-bootstrap/Breadcrumb';
import { Link } from "react-router-dom";
import "./breadcrumb.css"
const BreadcrumbWrap = ({pages}) => {
  return (
    <div className="breadcrumb-area bg-gray-3">
      <div className="container" style={{display:"flex",justifyContent:"center"}}>
        <div class="chapternav-wrapper">
			<ul class="chapternav-items">
				<li class="chapternav-item chapternav-item-macbook-air-m1">
					<a class="chapternav-link"  data-analytics-title="macbook air m1">
						<figure class="chapternav-icon"></figure>
						<span class="chapternav-label">MacBook Air (M1)</span>
					</a>
				</li>
				<li class="chapternav-item chapternav-item-macbook-air-m2">
					<a class="chapternav-link"  data-analytics-title="macbook air m2">
						<figure class="chapternav-icon"></figure>
						<span class="chapternav-label">MacBook Air (M2)</span>
					</a>
				</li>
				<Link to={process.env.PUBLIC_URL + "/compare"}>
				<li class="chapternav-item chapternav-item-compare">
					<a class="chapternav-link"  data-analytics-title="compare">
						<figure class="chapternav-icon"></figure>
						<span class="chapternav-label">Compare</span>
					</a>
				</li>
				</Link>
				<li class="chapternav-item chapternav-item-displays">
					<a class="chapternav-link"  data-analytics-title="displays">
						<figure class="chapternav-icon"></figure>
						<span class="chapternav-label">Displays</span>
					</a>
				</li>
				<li class="chapternav-item chapternav-item-accessories">
					<a class="chapternav-link"  data-analytics-title="accessories">
						<figure class="chapternav-icon"></figure>
						<span class="chapternav-label">Accessories</span>
					</a>
				</li>
				<Link to={process.env.PUBLIC_URL + "/apple-care"}>
				<li class="chapternav-item chapternav-item-accessories">
					<a class="chapternav-link"  data-analytics-title="accessories">
						<figure class="chapternav-icon"></figure>
						<span class="chapternav-label">AppleCare+ For Mac</span>
					</a>
				</li>
				</Link>
			</ul>
		</div>
      </div>
    </div>
  );
};

BreadcrumbWrap.propTypes = {
  pages: PropTypes.arrayOf(PropTypes.shape({
    label: PropTypes.string.isRequired,
    path: PropTypes.string.isRequired
  })).isRequired
}

export default BreadcrumbWrap;
